/* ------------------------------------------------------ */
/* ESTILOS PROPIOS */
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.app{
  background-color: #fcf5eb;
  height: 100vh;
}
.container1{
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center; 

}

/* HEADER */

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px;
  background-color: #fcf5eb;
  color: #000;
  font-size: 1.2rem;
  z-index: 5;
}

.header__left {
  display: flex;
  align-items: center;
}

.header__logo {
  width: 50px; 
  margin-right: 50px;
}

.header__title {
  font-size: 1.5rem;
  font-weight: bold;
}

.header__center {
  display: flex;
  align-items: center;
  margin: 0 auto;
  
}

.header__center__a{
  cursor: pointer;
  margin-right: 2rem;
}

.header__right {
  display: flex;
  align-items: center;
}

.header__right img{
  width: 60px;
}


/* REGISTRO*/

.registro-container {
  max-width: 400px;
  margin: 0px auto;
  padding: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #fff;
}

.registro-container h2 {
  font-size: 24px;
  margin-bottom: 20px;
  text-align: center;
}

.registro-container label {
  font-weight: bold;
}

.registro-container input[type="text"],
.registro-container input[type="file"],
.registro-container select {
  width: 100%;
  padding: 10px;
  margin: 5px 0 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
  box-sizing: border-box;
}

.registro-container input[type="file"] {
  cursor: pointer;
}

.registro-container input[type="radio"] {
  margin-right: 5px;
}

.registro-container img {
  margin-right: 10px;
  width: 50px; /* Hacer que todas las imágenes ocupen el 100% del ancho del contenedor */
  height: 50px; /* Establecer una altura fija para todas las imágenes */
  object-fit: cover;
  border-radius: 50%;
}

.registro-container button {
  display: block;
  width: 100%;
  padding: 10px;
  margin-top: 20px;
  border: none;
  border-radius: 5px;
  background-color: #128C7E;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
}

.registro-container button:hover {
  background-color: #075E54;
}

.registro-container button:active {
  background-color: #128C7E;
}

.image-container {
  flex: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 8%;
  
}

.image-container img {
  width: 80%;
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  border: 1px solid #ddd;
}



/* USUARIOS CONECTADOS */

.principal-container {
  max-width: 300px; 
  height: 80vh;
  margin: 20px auto; 
  background-color: #f5f5f5; 
  border-radius: 10px; 
  padding: 20px; 
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.principal-container h2 {
  font-size: 20px; 
  margin-bottom: 15px; 
  color: #333; 
}

.principal-container-lista {
  list-style: none; 
  padding: 0; 
}

.principal-container-item {
  font-size: 16px; 
  padding: 10px 0; 
  border-bottom: 1px solid #ddd; 
}

.principal-container-item:last-child {
  border-bottom: none; 
}

/* CHAT */
/* Chat.css */
.chat-container {
  display: flex;
  flex-direction: column;
  height: 100%;
}

.chat-messages {
  flex: 1;
  overflow-y: auto;
  padding: 10px;
  margin: 10px 10px 10px 0;
  border: 1px solid #ddd;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); 
}

.message {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.message img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin-right: 10px;
}

.message-text {
  background-color: #f5f5f5;
  padding: 8px 12px;
  border-radius: 10px;
  max-width: 70%;
}

.input-container {
  display: flex;
  align-items: center;
  padding: 10px;
  border-top: 1px solid #ddd;
}

.input-container input {
  flex: 1;
  padding: 8px;
  border: none;
  border-radius: 20px;
  margin-right: 10px;
}

.input-container button, .btn-chat {
  padding: 8px 16px;
  background-color: #128C7E;
  color: #fff;
  border: none;
  border-radius: 20px;
  cursor: pointer;
}

.btn-chat{
  height: 40PX;
}

.emoji-container {
  display: flex;
  overflow-x: auto;
  padding: 10px;
}

.emoji {
  cursor: pointer;
  margin-right: 10px;
}

.typing-indicator {
  padding: 10px;
  font-style: italic;
}



